<template>
  <!-- 干部类别定义 -->
  <el-container class="permission-assignment">
    <el-aside width='20%' class="permission-assignment-aside">
      <el-scrollbar :style="{'height':siteContentViewHeight - 10 + 'px' }">
        <org-tree ref="tree" :treeData="treeData" :expandAll="true" :defaultProps="defaultProps"
                  @node-click="orgTreeClick"></org-tree>
      </el-scrollbar>
    </el-aside>
    <el-main>
      <div class="permission-assignment-title">
        <span></span>
        <p>{{total}}条</p>
      </div>
      <el-form :inline="true" class="float-right">
        <el-form-item>
          <el-button type="primary" size="small" @click="addOrUpdateHandle()">添加</el-button>
        </el-form-item>
      </el-form>
      <el-table
        :data="dataList"
        v-loading="dataListLoading"
        element-loading-background="rgba(0, 0, 0, 0)"
        :height="siteContentViewHeight - 56+'px'"
        style="width: 100%;">
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column prop="leaderDesc" label="条件描述"></el-table-column>
        <el-table-column prop="leaderStatus" width="200" label="是否使用">
          <template slot-scope="scope">
            <span v-if="scope.row.leaderStatus == '1'" style="color:green;">启用</span>
            <span v-else style="color:red;">停用</span>
          </template>
        </el-table-column>
        <el-table-column width="150" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" plain @click="addOrUpdateHandle(scope.row)">修改</el-button>
            <el-button type="danger" size="mini" plain @click="deleteHandle(scope.$index,scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 弹窗, 新增 / 修改 -->
      <!--<add-or-update v-if="addOrUpdateVisible" ref="addOrUpdate" @refreshDataList="getDataList"></add-or-update>-->


      <el-dialog
        :title="title"
        :visible.sync="addListVisable"
        width="80%"
        v-if="addListVisable"
        :close-on-click-modal ="false"
      >
        <add-or-update :checkedData="checkedData" ref="addOrUpdate" @addback="addback"></add-or-update>
      </el-dialog>

    </el-main>
  </el-container>
</template>

<script>
  import AddOrUpdate from './add-or-update'
  import orgTree from "@/components/baseComponents/orgTree.vue";
  export default{
    props:['siteContentViewHeight'],
    components:{
      orgTree,AddOrUpdate
    },
    data(){
      return{
        treeData:[],
        dataList:[],
        dataListLoading:false,
        addOrUpdateVisible: false,
        addListVisable:false,
        currentRow:'',//选中的树数据
        defaultProps:{
          children: 'children',
          label: 'itemName'
        },
        total:0,
        operateType:'add'
      }
    },
    created () {
      this.getDicCodeItem()
    },
    methods:{
      getDicCodeItem(){
        //获取人员类别字典项
        let dicCode = 'LeaderType'
        var params = {
          dictCode:dicCode
        }
        this.$http({
          url: this.$http.adornUrl(this.API.getDictItemByCode),
          method: 'post',
          data: this.$http.adornData(params)
        }).then(({data}) => {
          if(data && data.code===0){
            this.treeData = data.data
          }else{
            this.$message.error(data.msg);
          }
        });

      },
      getDataList(id){
        this.dataListLoading = true
        let params ={
          leaderType:id
        }
        this.$http({
          url: this.$http.adornUrl(this.API.queryPersonLeaderconditionAll),
          method: 'post',
          data: this.$http.adornData(params)
        }).then(({data}) => {
          if(data && data.code===0){
            this.dataList = data.data
            this.total = data.total
            this.dataListLoading = false
          }else{
            this.$message.error(data.msg);
          }

        });
      },
      orgTreeClick(data) {
        this.getDataList(data.id);
        this.currentRow = data;
      },
      addOrUpdateHandle(row){// 新增 / 修改

        if(row){
          //编辑
          this.checkedData = this.currentRow;
          this.addListVisable = true;
          this.isEdit = true;
          this.title = '信息项编辑';
          this.operateType = 'edit'
          this.$nextTick(() => {
            this.$refs.addOrUpdate.getDataTree()
            this.$refs.addOrUpdate.initData(this.operateType)
            this.$refs.addOrUpdate.getDataList(row)
          })
        }else{
          //新增
          if(this.currentRow == ''){
            this.$message({
              message: '请选择干部类别',
              type: 'warning'
            });
          }else{
            this.checkedData = this.currentRow;
            this.addListVisable = true;
            this.isEdit = true;
            this.title = '信息项添加';
            this.operateType = 'add'
            this.$nextTick(() => {
              this.$refs.addOrUpdate.getDataTree()
              this.$refs.addOrUpdate.initData(this.operateType)
            })
          }

        }

      },
      addback(isRefresh){
        this.addListVisable = false;
        if(isRefresh){
          this.getDataList(this.currentRow.id);
        }
      },
      deleteHandle(index,id){
        this.$confirm('确定要删除该信息？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http({
            url: this.$http.adornUrl(this.API.deletePersonLeadercondition),
            method: 'post',
            data: this.$http.adornData({
              id:id
            })
          }).then(({data}) => {
            if(data && data.code===0){
              this.dataList.splice(index,1);
              this.$message.success('操作成功');
            }else{
              this.$message.error(data.msg);
            }
          });
        }).catch(() => {})
      }
    }
  }
</script>
<style lang="scss" scoped>
  .permission-assignment{
    &-title{
      float: left;
      padding-left: 10px;
      color: #f0992b;
      span{
        display: inline-block;
        width:10px;
        height: 10px;
        background: rgba(#f0992b,0.6);
        box-shadow: 0px 0px 6px 0px #f0992b;border-radius: 50%;}
      p{display: inline-block; padding-left: 10px;}
      padding-right: 20px;
    }
    &-aside{
      padding: 10px 10px 0;
      border-right: 1px solid #e6e6e6;
      .el-scrollbar{padding: 8px 0;}
    }
    .el-form--inline .el-form-item{margin-bottom: 10px;margin-top: 0px;}
  }
  >>> .el-main{
    padding-bottom: 0;
  }
</style>



